'use strict';

import { ACTIONS } from '../constants'

export default dispatch => {
	return {
		requestCategories: () => {
			dispatch({type: 'CATEGORIES_REQUEST'})
			dispatch((dispatch, getState) => {
				fetch('https://terezanov.ru:8080/api/categories')
				.then(r => r.json())
				.then(json => {
					console.log(json)
					return dispatch({
						type: 'CATEGORIES_RECEIVED',
						data: json.data.filter(category => category.name && category.name !== '""' )
					})
				})
			})
		},

		selectCategory: data => dispatch({
			type: 'CATEGORIES_SELECT', data
		}),

		searchQuery: query => {
			console.log('query', query)
			dispatch({type: 'BEERS_REQUEST'})
			dispatch((dispatch, getState) => {
				fetch(`https://terezanov.ru:8080/api/search?q=${query}&type=beer`)
				.then(r => r.json())
				.then(json => {
					let category = getState().categories.selected;
					console.log('searchQuery json:',json)
					let action = {
						type: 'BEERS_RECEIVED',
						data: category
						? json.data.filter(beer => (
							beer.style &&
							beer.style.categoryId &&
							beer.style.categoryId.toString() === category.toString()
						))
						: json.data
					}
					console.log('searchQuery action:', action)
					return dispatch(action)
				})
			})
		}

	}

};



