'use strict';

import 'react-hot-loader'
import React, { Component } from 'react'

import C from 'classnames'

import css from './SearchForm.scss'

class StylePage extends Component {

  constructor(props) {
    super(props)
  }

  onSubmit(event) {
    event.preventDefault();
    this.props.onSearchQuery(this.searchInput.value);
  }

  onSelect(event) {
    this.props.onCategorySelect(event.target.value)
  }

  render() {
    let categories = [{id: 0, name: 'Any beer category'}].concat(this.props.categories.data)
    return (
      <form
        className={css.search}
        onSubmit={this.onSubmit.bind(this)}>
        <input
          className={css.query}
          type="text"
          ref={input => this.searchInput = input}
          placeholder={'Your beer search query'}/>
        <button
          className={css.submit}
          type="submit">
          {
            '🔎'
          }
        </button>
        <select disabled={!!this.props.disabled}
          className={css.categories}
          onChange={this.onSelect.bind(this)}>
          {
            categories.map((category, key) => <option key={key} value={category.id}>{category.name}</option>)
          }
        </select>
      </form>
    );
  }
}

export default StylePage



