'use strict';

import 'react-hot-loader'
import React, { Component } from 'react'
// import { Router, Route, IndexRoute, Redirect, browserHistory } from 'react-router'

import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import C from 'classnames'

import css from './SearchPage.scss'

import actions from '../../actions/SearchPage'

//import UnderConstruction from '../../components/UnderConstruction'

import SearchForm from '../SearchForm'
import SearchResults from '../SearchResults';
// import SearchResultsDetailes from './components/search-result-detailes'
import Bookmarks from '../Bookmarks'

// import MainLayout from './components/main-layout'

@connect(
    state => ({state}),
    dispatch => {
      return actions(dispatch)
    }
)
class SearchPage extends Component {

  constructor(props) {
    super(props)
    console.log(props)
  }

  componentWillMount() {
    if(!this.props.state.categories.fetching){
      this.props.requestCategories()
    }
  }

  onCategorySelect(category) {
    console.log('category:', category)
    this.props.selectCategory(category)
  }

  onSearchQuery(query) {
    this.props.searchQuery(query)
  }

  render() {
    let categories = this.props.state.categories;
    let beers = this.props.state.beers.data;
    console.log('BEERS:', beers)
    console.log('BEERS with labels:', beers.filter(beer => !!beer.labels))
    console.log('BEERS names:', beers.map(beer => beer.name))
    return  (
      <div className={css.page}>
        <SearchForm
          categories={categories}
          onCategorySelect={this.onCategorySelect.bind(this)}
          onSearchQuery={this.onSearchQuery.bind(this)}/>
        <div className={css.pageBody}>
          <SearchResults beers={beers}/>
          <Bookmarks />
        </div>
      </div>
    )
  }
}

export default SearchPage



